.footer-body{
    position: fixed;
    left: 0;
    font-size: 0.75em;
    bottom: 0;
    height: 10vh;
    width: 100%;
    background-color: #181830;
    color: #FFFFFF;
    align-items: center;
    justify-content: space-around;
    display: grid;
    grid-template-columns: auto auto auto;
}

.grid-item{
    cursor: pointer;
}

.grid-item:hover{
    color:#4188ff;
    border-bottom: #4188ff;
}