.nav-body{
    background-color: #181830;
    color: #FFFFFF;
    height: 10vh;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    

}

.nav-head{
    font-size: 2em;
    margin-left: 0.5em;
    /* background-color:crimson; */
}




.nav-list{
    display: flex;
    margin-top: 1.5em;
    /* background-color: crimson; */

}
.nav-item{
    margin: 0 1em 0 1em;
    cursor: pointer;
    padding-bottom: 1em;
    
    
    
}

.nav-item:hover{
    color:#4188ff;
    border-bottom: #4188ff;
}

